header {
  /* grid-area: header; */
  font-size: 4rem;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  height: 90px;
  width: 100%;
}

.body {
  padding-top: 90px;
  padding-bottom: 90px;
  display: flex;
  justify-content: center;
}

/* BarkLog {
  grid-area: barkLog;
